:root {
  --font-raleway: "Raleway", sans-serif;
  --font-poppins: "Poppins", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;
}

:root {
  --button: hsl(360, 100%, 69%);
  --client: hsl(26, 96%, 56%);
  --promo: hsl(264, 100%, 50%);
  --white: hsl(0, 0%, 100%);
  --copyright: hsl(0, 0%, 79%);
  --light-text: hsl(0, 0%,55%);
  --button-shadow: hsl(0, 0%, 44%);
  --dark-text: hsl(0, 0%,20%);
  --comment: hsl(0, 0%, 13%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  color: var(--white);
  font-family: var(--font-raleway);
  min-height: 150vh;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 3.125rem;
  margin-bottom: 1.125rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 4.375rem;
  }
}

h2 {
  font-size: 2.25rem;
  margin-bottom: 2rem;
}

h3 {
  font-size: 1.5625rem;
  margin-bottom: 1rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--white);
  font-size: 1.125rem;
}

.container {
  padding: 0 1.625rem;
  max-width: 73.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.promo {
  background-image: url("/images/background.png");
  background-color: var(--promo);
  background-repeat: no-repeat;
  background-position: 50% -75%;
  background-size: 150vh;
  height: 100vh;
}
@media (min-width: 40em) {
  .promo {
    background-position: 50% 100%;
    background-size: 250vh;
  }
}

.button {
  background-color: var(--button);
  box-shadow: 0 1px 0 var(--button-shadow);
  border-radius: 22.5px;
  padding-top: 0.5625rem;
  padding-right: 2rem;
  padding-bottom: 0.75rem;
  padding-left: 2rem;
}

.header {
  display: flex;
  flex-wrap: wrap;
  padding-top: 1.5625rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
    padding-top: 3.125rem;
  }
}
.header__menu a {
  font-size: 1.125rem;
  font-weight: 500;
}
.header__menu a:not(:last-child) {
  margin-right: 3.875rem;
}